








































@import "@design";

.c-checkbox {
  $self: &;
  $size: 16px;

  display: flex;
  align-items: center;
  justify-content: center;
  flex: none;
  width: $size;
  height: $size;
  border: 1px solid $light-blue-grey;
  border-radius: 2px;
  transition: border-color 0.2s ease;
  cursor: pointer;

  &:hover,
  &--checked {
    border-color: rgba($charcoal-grey, 0.56);
  }
}
