


























































































@import "@design";

.c-checkbox-label {
  $self: &;

  display: flex;
  align-items: center;

  &__text {
    margin-left: 8px;
    font: 14px/2.3 $font-main;
    color: $charcoal-grey;
    cursor: default;

    #{$self}--disabled & {
      opacity: 0.5;
    }
  }
}
