












































































































@import "@design";

.c-title {
  $self: &;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  &--h1 {
    margin: 24px 0;
  }

  &--inline {
    display: inline;
  }

  &--block {
    display: block;
  }

  &--error {
    color: $light-burgundy;
  }

  &--text {
    font-size: 14px;
  }

  &--caption {
    font-size: 12px;
  }

  &--subheader {
    font-weight: 500;
  }

  &--secondary {
    color: rgba($charcoal-grey, 0.5);
  }

  &--hint {
    color: rgba($charcoal-grey, 0.4);
  }
}
