

























































































.header {
  background: #fff;
  border-bottom: 2px solid black;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__action-place {
    height: 100%;
    justify-content: space-between;

    &--row {
      align-items: baseline;
      margin-top: 5px;
    }
  }

  &__user-name {
    align-items: flex-end
  }
}
